// lib/api.ts
import axios from 'axios';

export const API_BASE = 'http://localhost:4000/anime/zoro';

// ============================
// Tipe Anime yang Diperluas
// ============================
export interface Anime {
  animeId?: string; 
  mal_id?: string | number; 
  title: string;
  image?: string; // URL gambar sederhana (dari Consumet)
  url: string;
  type: string;
  episodes?: number;
  duration?: string;
  sub?: number;
  dub?: number;
  japaneseTitle?: string;
  nsfw?: boolean;

  // Properti Detail (dari Jikan/MAL)
  title_english?: string; // BARU: Digunakan di watch page
  synopsis?: string;      // BARU: Digunakan di watch page
  score?: number | null; 
  genres?: Array<{ 
    mal_id: number;
    type: string;
    name: string;
    url: string;
  }>;

  // Struktur gambar kompleks (dibuat opsional agar kompatibel dengan data Consumet)
  images?: { 
    jpg: {
      image_url: string;
      small_image_url?: string; 
      large_image_url?: string; 
    };
    // Dibuat opsional agar tidak menimbulkan error 2430
    webp?: { 
      image_url: string;
      small_image_url: string;
      large_image_url: string;
    };
  };
}

// ============================
// Tipe Episode Streaming
// ============================

export interface EpisodeLinks {
  sources: Array<{ url: string; quality?: string; isM3U8?: boolean }>;
  subtitles: Array<{ url: string; lang?: string }>;
  animeId: string;
  episodeId: string;
}

// ============================
// Helper function untuk fetch
// ============================
async function fetchAnime(endpoint: string, page: number = 1): Promise<Anime[]> {
  try {
    const res = await axios.get(`${API_BASE}/${endpoint}?page=${page}`);
    const data = res.data?.results || [];
    return data.map((item: any) => ({
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw,
    }));
  } catch (err) {
    console.error(`Error in fetchAnime(${endpoint}):`, err);
    return [];
  }
}

// ============================
// API functions
// ============================

export async function getRecentEpisodes(page: number = 1): Promise<Anime[]> {
  return fetchAnime('recent-episodes', page);
}

export async function getPopularAnime(page: number = 1): Promise<Anime[]> {
  return fetchAnime('popular', page);
}

export async function getTopAnime(page: number = 1): Promise<Anime[]> {
  return fetchAnime('top', page);
}

export async function searchAnime(query: string, page: number = 1): Promise<Anime[]> {
  try {
    const res = await axios.get(`${API_BASE}/search?query=${encodeURIComponent(query)}&page=${page}`);
    const data = res.data?.results || [];
    return data.map((item: any) => ({
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw
    }));
  } catch (err) {
    console.error('Error in searchAnime:', err);
    return [];
  }
}

export async function getAnimeDetail(animeId: string): Promise<Anime | null> {
  try {
    const res = await axios.get(`${API_BASE}/info/${animeId}`);
    const item = res.data;
    if (!item) return null;

    // Mapping properti dasar Consumet
    return {
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw,
      
      // Jika API /info mengembalikan detail tambahan, map di sini:
      title_english: item.title_english,
      synopsis: item.synopsis,
      // images: item.images, // Tambahkan jika tersedia di respons API
    };
  } catch (err) {
    console.error('Error in getAnimeDetail:', err);
    return null;
  }
}

export async function getEpisodeStreamingLinks(animeId: string, episodeId: string): Promise<EpisodeLinks | null> {
    try {
        const res = await axios.get(`${API_BASE}/watch/${episodeId}`);
        const data = res.data;

        if (!data || data.sources.length === 0) return null;

        return {
            sources: data.sources || [],
            subtitles: data.subtitles || [],
            animeId: animeId,
            episodeId: episodeId,
        } as EpisodeLinks;

    } catch (err) {
        console.error(`Error fetching streaming links for ${episodeId}:`, err);
        return null;
    }
}