'use client'

import { useState, useEffect } from 'react'
// âœ… Pastikan Anda mengimpor tipe 'Anime' dari '@/lib/api'
import { getPopularAnime, Anime } from '@/lib/api' 
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { FunSkeletonGrid } from '@/components/skeleton-loader'

export default function PopularPage() {
  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchPopular = async () => {
      setLoading(true)
      
      // Mengasumsikan getPopularAnime mengembalikan Promise<Anime[]>
      // Jika ia mengembalikan Promise<{ data: Anime[] }>, tipe di lib/api perlu diubah.
      // Berdasarkan error, kita asumsikan ia mengembalikan Anime[]
      const data: Anime[] = await getPopularAnime(1)
      
      // âœ… PERBAIKAN: Hapus .data karena 'data' sudah berupa array Anime[]
      setAnimes(data) 
      
      setLoading(false)
    }
    fetchPopular()
  }, [])

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-2 text-foreground">Popular Anime</h1>
        <p className="text-muted-foreground mb-8">
          Explore the latest released episodes (based on Zoro/Consumet API)
        </p>

        {/* --- Loading State --- */}
        {loading ? (
          <FunSkeletonGrid count={25} />
        ) : animes.length > 0 ? (
          
          /* --- Anime Grid --- */
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
            {animes.map(anime => (
              // âœ… Menggunakan key yang unik
              <AnimeCard 
                key={anime.animeId || anime.mal_id} 
                anime={anime} 
              />
            ))}
          </div>
        ) : (
          /* --- No Data State --- */
          <p className="text-center text-muted-foreground">No recent or popular anime found from the provider.</p>
        )}
      </div>
    </main>
  )
}